import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const routes: Routes = [
  {
    path: '',
    loadChildren: () => import('./../../tabs/tabs.module').then(m => m.TabsPageModule)
  },
  {
    path: 'styleguide',
    loadChildren: () => import('./styleguide/styleguide.module').then(m => m.StyleguidePageModule)
  },
  {
    path: 'settings',
    loadChildren: () => import('./settings/settings.module').then(m => m.SettingsPageModule)
  },
  {
    path: 'settings/profile/edit',
    loadChildren: () => import('./profile/edit/edit.module').then(m => m.EditPageModule)
  },
  {
    path: 'payments/detail',
    loadChildren: () => import('./payments/payment-detail/payment-detail.module').then(m => m.PaymentDetailPageModule)
  },
  {
    path: 'wallet/tron-network',
    loadChildren: () => import('./wallet/tron-network/tron-network.module').then(m => m.TronNetworkPageModule)
  },
  {
    path: 'send',
    loadChildren: () => import('./wallet/send/send.module').then(m => m.SendPageModule)
  },
  {
    path: 'wallet/eth-network',
    loadChildren: () => import('./wallet/ethwallet-network/ethwallet-network.module').then(m => m.EthwalletNetworkPageModule)
  },



  {
    path: 'ethwallet-network',
    loadChildren: () => import('./wallet/ethwallet-network/ethwallet-network.module').then(m => m.EthwalletNetworkPageModule)
  },

  {
    path: 'wallet/ftm-network',
    loadChildren: () => import('./wallet/ftm-network/ftm-network.module').then(m => m.FtmNetworkPageModule)
  },
  {
    path: 'ftm-network',
    loadChildren: () => import('./wallet/ftm-network/ftm-network.module').then(m => m.FtmNetworkPageModule)
  },
  {
    path: 'wallet/usdc-network',
    loadChildren: () => import('./wallet/usdc-network/usdc-network.module').then( m => m.UsdcNetworkPageModule)
  },
  {
    path: 'trade/:coin',
    loadChildren: () => import('./exchange/trade/trade.module').then( m => m.TradePageModule)
  },
  {
    path: 'pairs',
    loadChildren: () => import('./exchange/pairs/pairs.module').then( m => m.PairsPageModule)
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SecureRoutingModule { }
